
import { mapGetters } from 'vuex'
import VueQr from 'vue-qr'
import XButton from 'vux/src/components/x-button/index.vue'

export default {
name: 'make',
data () {
return {
havePage: false,
name: '',
addresses:'',
data:''
}
},
components: {
XButton,
VueQr
},
created: function () {
var that = this
that.getParams ()
},
computed: {
...mapGetters([
'info',
'current'
])
},
methods: {
getParams () {
var that = this
that.$store.dispatch('UserInit', '');
if(that.info[that.current] == '' || that.info[that.current] == undefined) {
that.havePage = false;
} else {
that.havePage = true;
that.data = that.info[that.current]
that.name = that.data.name
that.addresses = that.data.addresses
}
},
handleSuccess (e) {
var that = this
if(e.text) {
this.$vux.toast.show({
type: 'success',
text: '地址复制成功',
})
}
}
}
}



